.navigation-container {
  position: fixed;
  right: 5em;
  width: 400px;
  display: -ms-flexbox;
  display: flex;
  margin-top: -50px; }

.navigation-container a {
  margin-left: 30px;
  font-size: 2em;
  color: black;
  cursor: pointer; }

.navigation-container a:hover {
  color: white;
  background-color: black;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s; }



